import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Login from '../components/login'
import Other from '../components/other'

const routes = [
  { path: '/', component: Login },
  { path: '/other', component: Other }
]

export default new VueRouter({
  routes
})



